				<!-- Ë¥≠Áâ©ËΩ¶ÂïÜÂìÅÂàóË°® -->
					<view class="cart-item" v-for="(item, index) in cartItems" :key="item._id">
						<view class="item-checkbox" @click="toggleItemSelect(item._id, !item.selected)">
						<view class="custom-checkbox" :class="{ checked: item.selected }">
