				<view class="empty-cart" v-if="cartItems.length === 0 && !loading">
				<text class="iconfont icon-shopping-cart empty-cart-icon"></text>
				<text class="empty-cart-text">Ë¥≠Áâ©ËΩ¶ËøòÊòØÁ©∫ÁöÑÔºåÂéªÊåëÈÄâÂøÉ‰ª™ÁöÑÂïÜÂìÅÂêß~</text>
				<view class="go-shopping-btn" @click="goShopping">ÂéªË¥≠Áâ©</view>
			</view>
				
				<!-- Âä†ËΩΩÁä∂ÊÄÅ -->
				<view class="loading-state" v-if="loading">
					<text>Âä†ËΩΩ‰∏≠...</text>
				</view>
			</template>
		</view>
		
		<!-- Â∫ïÈÉ®ÁªìÁÆóÊ†è -->
		<view class="cart-footer" v-if="isLoggedIn && cartItems.length > 0">
			<view class="select-all" @click="toggleSelectAll">
				<view class="custom-checkbox" :class="{ checked: isAllSelected }">
					<image src="/static/icons/shopping icon/check.svg" v-if="isAllSelected"></image>
				</view>
				<text>ÂÖ®ÈÄâ</text>
			</view>
			<view class="cart-total">
				<text class="total-price">¬•{{totalPrice}}</text>
				<text class="price-details">ÊÄªËÆ°: ¬•{{totalPrice}} ¬∑ Â∑≤‰ºòÊÉ†: ¬•{{discount}}</text>
			</view>
			<view class="checkout-btn" @click="checkout">ÁªìÁÆó({{selectedCount}})</view>
		</view>
