<script setup>
	import { ref, computed, onMounted, onUnmounted, getCurrentInstance, nextTick } from 'vue'
	import { onShow } from '@dcloudio/uni-app'
	import { getAllProducts } from '@/data/products.js'
	
	const { proxy } = getCurrentInstance()
	
	// ÂìçÂ∫îÂºèÊï∞ÊçÆ
	const cartItems = ref([])
	const loading = ref(false)
	const discount = ref(3)
	const userToken = ref('')
	const userInfo = ref(null)
	
	const recommendItems = ref([
					{
						id: 4,
						name: 'Áë∂Â±±ÈáéÁîüÈ¶ôËèá',
						price: 78,
						image: '/static/static2/3944.jpg_wh860.jpg'
					},
					{
						id: 5,
						name: 'Áë∂Â±±Âè§Ê≥ïÂ±±Ëå∂Ê≤π',
						price: 188,
						image: '/static/static2/v2-a582c6ef03ee0473ff546458328c18b6_720w.png'
					},
					{
						id: 6,
						name: 'Áë∂Â±±ÊúâÊú∫Á®ªÁ±≥',
						price: 128,
						image: '/static/static2/photo-1515339760107-1952b7a08454.avif'
					}
	])
	
	// ËÆ°ÁÆóÂ±ûÊÄß - Âü∫‰∫éÊú¨Âú∞ÂìçÂ∫îÂºèÊï∞ÊçÆËÆ°ÁÆó
	const isLoggedIn = computed(() => {
		return !!userToken.value
	})
	
	const selectedCount = computed(() => {
		return cartItems.value
			.filter(item => item.selected)
			.reduce((total, item) => total + item.quantity, 0)
	})
	
	const totalPrice = computed(() => {
		return cartItems.value
					.filter(item => item.selected)
			.reduce((total, item) => total + (item.productInfo.price || 0) * item.quantity, 0)
	})
	
	const isAllSelected = computed(() => {
		return cartItems.value.length > 0 && cartItems.value.every(item => item.selected)
	})
	
	// Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
	const checkLoginStatus = () => {
		userToken.value = uni.getStorageSync('userToken') || ''
		userInfo.value = uni.getStorageSync('userInfo') || null
	}
	
	// Âä†ËΩΩË¥≠Áâ©ËΩ¶Êï∞ÊçÆ
	const loadCartData = async () => {
		if (!userToken.value) {
			cartItems.value = []
			return
		}
		
		loading.value = true
		
		try {
			const result = await uniCloud.callFunction({
				name: 'user-cart',
				data: {
					action: 'getCart',
					token: userToken.value
				}
			})
			
			if (result.result.errCode === 0) {
				cartItems.value = result.result.data || []
			} else {
				console.error('Ëé∑ÂèñË¥≠Áâ©ËΩ¶Â§±Ë¥•:', result.result.errMsg)
				if (result.result.errCode === 10001) {
					// tokenÊó†ÊïàÔºåÊ∏ÖÈô§ÁôªÂΩïÁä∂ÊÄÅ
					uni.removeStorageSync('userToken')
					uni.removeStorageSync('userInfo')
					checkLoginStatus()
				}
			}
		} catch (error) {
			console.error('Âä†ËΩΩË¥≠Áâ©ËΩ¶Êï∞ÊçÆÂ§±Ë¥•:', error)
			uni.showToast({
				title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
				icon: 'none'
			})
		} finally {
			loading.value = false
		}
	}
	
			// ÂàáÊç¢ÂïÜÂìÅÈÄâ‰∏≠Áä∂ÊÄÅ
	const toggleItemSelect = async (cartItemId, selected) => {
		try {
			const result = await uniCloud.callFunction({
				name: 'user-cart',
				data: {
					action: 'toggleSelect',
					token: userToken.value,
					cartItemId,
					selected
				}
			})
			
			if (result.result.errCode === 0) {
				await loadCartData()
			} else {
				uni.showToast({
					title: result.result.errMsg,
					icon: 'none'
				})
			}
		} catch (error) {
			console.error('ÂàáÊç¢ÈÄâ‰∏≠Áä∂ÊÄÅÂ§±Ë¥•:', error)
		}
	}
	
			// ÂàáÊç¢ÂÖ®ÈÄâ
	const toggleSelectAll = async () => {
		const newSelectState = !isAllSelected.value
		
		// ÊâπÈáèÊõ¥Êñ∞ÊâÄÊúâÂïÜÂìÅÁöÑÈÄâ‰∏≠Áä∂ÊÄÅ
		const promises = cartItems.value.map(item => 
			toggleItemSelect(item._id, newSelectState)
		)
		
		await Promise.all(promises)
	}
	
	// Êõ¥Êñ∞ÂïÜÂìÅÊï∞Èáè
	const updateQuantity = async (cartItemId, newQuantity) => {
		try {
			const result = await uniCloud.callFunction({
				name: 'user-cart',
				data: {
					action: 'updateQuantity',
					token: userToken.value,
					cartItemId,
					newQuantity
				}
			})
			
			if (result.result.errCode === 0) {
				await loadCartData()
			} else {
				uni.showToast({
					title: result.result.errMsg,
					icon: 'none'
				})
			}
		} catch (error) {
			console.error('Êõ¥Êñ∞Êï∞ÈáèÂ§±Ë¥•:', error)
		}
	}
	
			// ÂáèÂ∞ëÂïÜÂìÅÊï∞Èáè
	const decreaseQuantity = async (cartItemId, currentQuantity) => {
		if (currentQuantity > 1) {
			await updateQuantity(cartItemId, currentQuantity - 1)
				} else {
					uni.showModal({
						title: 'ÊèêÁ§∫',
						content: 'Á°ÆÂÆöË¶ÅÁßªÈô§ËØ•ÂïÜÂìÅÂêóÔºü',
				success: async (res) => {
							if (res.confirm) {
						await removeItem(cartItemId)
							}
						}
			})
		}
				}
	
			// Â¢ûÂä†ÂïÜÂìÅÊï∞Èáè
	const increaseQuantity = async (cartItemId, currentQuantity) => {
		await updateQuantity(cartItemId, currentQuantity + 1)
	}
	
	// Â§ÑÁêÜÊï∞ÈáèËæìÂÖ•Ê°ÜÂèòÂåñ
	const onQuantityChange = async (cartItemId, newQuantity) => {
		const quantity = parseInt(newQuantity)
		if (quantity > 0) {
			await updateQuantity(cartItemId, quantity)
		} else {
			await removeItem(cartItemId)
		}
	}
	
			// ÁßªÈô§ÂïÜÂìÅ
	const removeItem = async (cartItemId) => {
				uni.showModal({
					title: 'ÊèêÁ§∫',
					content: 'Á°ÆÂÆöË¶ÅÁßªÈô§ËØ•ÂïÜÂìÅÂêóÔºü',
			success: async (res) => {
						if (res.confirm) {
					try {
						const result = await uniCloud.callFunction({
							name: 'user-cart',
							data: {
								action: 'removeItem',
								token: userToken.value,
								cartItemId
							}
						})
						
						if (result.result.errCode === 0) {
							await loadCartData()
							uni.showToast({
								title: 'Â∑≤ÁßªÈô§',
								icon: 'success'
							})
						} else {
							uni.showToast({
								title: result.result.errMsg,
								icon: 'none'
							})
						}
					} catch (error) {
						console.error('ÁßªÈô§ÂïÜÂìÅÂ§±Ë¥•:', error)
					}
						}
					}
		})
	}
	
			// ‰ªéÊé®ËçêÊ∑ªÂä†ÂïÜÂìÅ
	const addToCartFromRecommend = async (item) => {
		if (!userToken.value) {
			uni.showToast({
				title: 'ËØ∑ÂÖàÁôªÂΩï',
				icon: 'none'
			})
			return
		}
		
		try {
			const result = await uniCloud.callFunction({
				name: 'user-cart',
				data: {
					action: 'addToCart',
					token: userToken.value,
					productId: item.id,
						quantity: 1,
					productInfo: item
				}
			})
				
			if (result.result.errCode === 0) {
				await loadCartData()
				uni.showToast({
					title: 'Â∑≤Âä†ÂÖ•Ë¥≠Áâ©ËΩ¶',
					icon: 'success'
				})
			} else {
				uni.showToast({
					title: result.result.errMsg,
					icon: 'none'
				})
			}
		} catch (error) {
			console.error('Ê∑ªÂä†Âà∞Ë¥≠Áâ©ËΩ¶Â§±Ë¥•:', error)
		}
	}
	
	// Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
	const goToLogin = () => {
		uni.navigateTo({
			url: '/pages/login/login'
		})
	}
	
			// Ë∑≥ËΩ¨Âà∞ÂïÜÂüéÈ°µÈù¢
	const goShopping = () => {
				uni.switchTab({
					url: '/pages/shopping/shopping'
		})
	}
	
			// ÁªìÁÆó
	const checkout = () => {
		if (selectedCount.value === 0) {
					uni.showToast({
						title: 'ËØ∑ÈÄâÊã©ÂïÜÂìÅ',
						icon: 'none'
			})
			return
				}
				
				// Ëé∑ÂèñÈÄâ‰∏≠ÁöÑÂïÜÂìÅ
		const selectedItems = cartItems.value.filter(item => item.selected)
		
		// Â∞ÜÈÄâ‰∏≠ÁöÑÂïÜÂìÅÊï∞ÊçÆ‰º†ÈÄíÂà∞ÁªìÁÆóÈ°µÈù¢
		uni.setStorageSync('checkout_items', selectedItems)
				
				// Ë∑≥ËΩ¨Âà∞Á°ÆËÆ§ËÆ¢ÂçïÈ°µÈù¢
				uni.navigateTo({
					url: '/pages/shopping/checkout/checkout'
		})
	}
	
	// ÁîüÂëΩÂë®Êúü
	onMounted(async () => {
		checkLoginStatus()
		if (userToken.value) {
			await loadCartData()
			}
	})
	
	onUnmounted(() => {
		// ÁßªÈô§‰∫ã‰ª∂ÁõëÂê¨
		uni.$off('cartUpdated', handleCartUpdate)
	})
	
	// È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Ë¥≠Áâ©ËΩ¶Êï∞ÊçÆ
	onShow(async () => {
		checkLoginStatus()
		if (userToken.value) {
			await loadCartData()
	}
	})
</script>

